<template>
  <el-form ref="form" :model="form" label-width="80px" :rules="addFormRules" @submit.prevent="onSubmit" style="margin:20px;width:60%;min-width:600px;">
    <el-form-item label="" prop="infoName">
      <el-input v-model="form.infoName" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" prop="stuNumber">
      <el-input v-model="form.stuNumber"></el-input>
    </el-form-item>
    <el-form-item label="" prop="stuContent">
      <el-input type="textarea" v-model="form.stuContent"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-col :span="11">
        <el-date-picker type="date" placeholder="" v-model="form.theTime" style="width: 100%;"></el-date-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="">
      <el-col :span="11">
        <el-date-picker type="date" placeholder="" v-model="form.endTime" style="width: 100%;"></el-date-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="">
      <el-radio-group v-model="form.stuOff">
        <el-radio class="radio" :label="1"></el-radio>
        <el-radio class="radio" :label="0"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label=" / ">
      <el-input type="textarea" v-model="form.stuReason"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit"></el-button>
      <el-button @click.native.prevent></el-button>
    </el-form-item>
  </el-form>
</template>

<script>

    import {addStuInfo} from '../../api/api';

    export default {
        data() {
            return {
//			    info
                form: {
                    infoName: '',
                    stuNumber: '',
                    theTime: '',
                    endTime: '',
                    stuContent: '',
                    stuOff: '',
                    stuReason: ''
                },
//				
                addFormRules: {
                    infoName: [
                        {required: true, message: '', trigger: 'blur'}
                    ], stuNumber: [
                        {required: true, message: '', trigger: 'blur'}
                    ], stuContent: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            onSubmit() {
                this.$confirm('', '', {}).then(() => {
                    let para = Object.assign({}, this.form);
                    console.log(para);
                    addStuInfo(para).then((res) => {
                        console.log(res);
                        if (res.data.code === "SUCCESS") {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                        } else {
                            this.$message({
                                message: '',
                                type: 'warning'
                            });
                        }
                        this.$refs['form'].resetFields();
                    });
                });
            }
        }
    }

</script>


