<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.StuNumber" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getUsers"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="getStuMessageList" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="stuName" label="" width="120" sortable>
      </el-table-column>
      <el-table-column prop="stuSex" label="" width="100" :formatter="formatSex" sortable>
      </el-table-column>
      <el-table-column prop="stuAge" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="stuNumber" label="" width="180" sortable>
        <template scope="scope">
          <router-link :to="'/info_list?stuNumber='+scope.row.stuNumber">{{ scope.row.stuNumber }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="classNum" label="" width="180" sortable>
      </el-table-column>
      <el-table-column prop="className" label="" min-width="100" sortable>
      </el-table-column>
      <el-table-column label="" width="150">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="stuMesDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!--<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>-->
      <!---->
      <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="table.page"
              :page-sizes="[10, 25, 50, 100]"
              :page-size="table.size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="table.data.length" style="float:right">
      </el-pagination>
    </el-col>


    <!---->
    <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="stuName">
          <el-input v-model="editForm.stuName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="editForm.stuSex">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="editForm.stuAge" :min="0" :max="200"></el-input-number>
        </el-form-item>
        <!--<el-form-item label="">-->
        <!--<el-date-picker type="date" placeholder="" v-model="editForm.birth"></el-date-picker>-->
        <!--</el-form-item>-->
        <el-form-item label="" prop="stuNumber">
          <el-input v-model="editForm.stuNumber" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="classNum">
          <el-input v-model="editForm.classNum" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="stuName">
          <el-input v-model="addForm.stuName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="addForm.stuSex">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="addForm.stuAge" :min="0" :max="200"></el-input-number>
        </el-form-item>
        <!--<el-form-item label="">-->
        <!--<el-date-picker type="date" placeholder="" v-model="addForm.birth"></el-date-picker>-->
        <!--</el-form-item>-->
        <el-form-item label="" prop="stuNumber">
          <el-input v-model="addForm.stuNumber" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="classNum">
          <el-input v-model="addForm.classNum" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
    import util from '../../common/js/util'
    //import NProgress from 'nprogress'
    import {getStuMessageList, removeStu, batchRemoveStu, editStu, addStu} from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    StuNumber: ''
                },
                search: {
                    keyword: ''
                },
                table: {
                    loading: false,
                    total: 0,
                    size: 25,
                    page: 1,
                    pageSize: 10,
                    data: []
                },
                listLoading: false,
                sels: [],//

                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    stuName: [
                        {required: true, message: '', trigger: 'blur'}
                    ], stuNumber: [
                        {required: true, message: '', trigger: 'blur'}
                    ], classNum: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                //
                editForm: {
                    id: 0,
                    stuName: '',
                    stuSex: -1,
                    stuAge: 0,
                    stuNumber: '',
                    classNumber: ''
                },

                addFormVisible: false,//
                addLoading: false,
                addFormRules: {
                    stuName: [
                        {required: true, message: '', trigger: 'blur'}
                    ], stuNumber: [
                        {required: true, message: '', trigger: 'blur'}
                    ], classNum: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                //
                addForm: {
                    stuName: '',
                    stuSex: -1,
                    stuAge: 0,
                    stuNumber: '',
                    classNumber: ''
                }

            }
        },
        computed: {
            getStuMessageList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size, keyword = self.search.keyword;
                var v = data.filter(function (p) {
                    return (p.stuName && p.stuName.indexOf(keyword) !== -1)
                        || (p.stuSex && p.stuSex.indexOf(keyword) !== -1)
                        || (p.stuAge && p.stuAge.indexOf(keyword) !== -1)
                        || (p.stuNumber && p.stuNumber.indexOf(keyword) !== -1)
                        || (p.classNum && p.classNum.indexOf(keyword) !== -1)
                        || (p.className && p.className.indexOf(keyword) !== -1);
                });
                self.total = v.length;
                v = v.slice((page - 1) * size, page * size);
                return v;
            },
        },
        methods: {
            //
            formatSex: function (row, column) {
                return row.stuSex === 1 ? '' : row.stuSex === 0 ? '' : '';
            },
            handleSizeChange(v) {
                this.table.size = v;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            // 
            getUsers() {
                let para = {
                    stuNumber: this.filters.StuNumber
                };
                this.listLoading = true;
                //
                getStuMessageList(para).then((data) => {
                    this.table.data = data;
                    this.listLoading = false;
                });
            },
            //
            stuMesDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = {stuNumber: row.stuNumber};
                    removeStu(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        if (res.data.code === "SUCCESS") {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                        } else {
                            this.$message({
                                message: '',
                                type: 'warning'
                            });
                        }
                        this.getUsers();
                    });
                }).catch(() => {

                });
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //
            handleAdd: function () {
                this.addFormVisible = true;
                this.addForm = {
                    stuName: '',
                    stuSex: -1,
                    stuAge: 0,
                    stuNumber: '',
                    classNum: ''
                };
            },
            //
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.editForm);
//							para.birth = (!para.birth || para.birth === '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            editStu(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                if (res.data.code === "SUCCESS") {
                                    this.$message({
                                        message: '',
                                        type: 'success'
                                    });
                                } else {
                                    this.$message({
                                        message: '',
                                        type: 'warning'
                                    });
                                }
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.addLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.addForm);
                            console.log(para);
//							para.birth = (!para.birth || para.birth === '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            addStu(para).then((res) => {
                                this.addLoading = false;
                                console.log(res);
                                //NProgress.done();
                                if (res.data.code === "SUCCESS") {
                                    this.$message({
                                        message: '',
                                        type: 'success'
                                    });
                                } else {
                                    this.$message({
                                        message: '',
                                        type: 'warning'
                                    });
                                }
                                this.$refs['addForm'].resetFields();
                                this.addFormVisible = false;
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = {ids: ids};
                    batchRemoveStu(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getUsers();
                    });
                }).catch(() => {

                });
            }
        },
        mounted() {
            this.getUsers();
        }
    }

</script>

<style scoped>

</style>


