<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <!--<el-form-item>-->
        <!--<el-input v-model="filters.StuNumber" placeholder=""></el-input>-->
        <!--</el-form-item>-->
        <!--<el-form-item>-->
        <!--<el-button type="primary" v-on:click="getUsers"></el-button>-->
        <!--</el-form-item>-->
        <el-form-item>
          <router-link :to="'/classMes'" style="background-color: #717AD2;color: #f4f4f4;padding: 10px;border-radius:5px;"></router-link>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="getClassInfoList" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="classNum" width="120" label="" sortable>
      </el-table-column>
      <el-table-column prop="theTime" width="120" label="" sortable>
      </el-table-column>
      <el-table-column prop="classContent" label="" sortable>
      </el-table-column>
      <el-table-column label="" width="150">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <!--<el-button type="danger" size="small" @click="stuInfoDel(scope.$index, scope.row)"></el-button>-->
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!--<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>-->
      <!---->
      <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="table.page"
              :page-sizes="[10, 25, 50, 100]"
              :page-size="table.size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="table.data.length" style="float:right">
      </el-pagination>
    </el-col>

    <!---->
    <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="infoName">
          <el-input v-model="editForm.infoName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuNumber">
          <el-input v-model="editForm.stuNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="stuContent">
          <el-input type="textarea" v-model="editForm.stuContent"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker type="date" placeholder="" v-model="editForm.theTime" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="11">
            <el-date-picker type="date" placeholder="" v-model="editForm.endTime" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="editForm.stuOff">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label=" / ">
          <el-input type="textarea" v-model="editForm.stuReason"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>


  </section>
</template>

<script>
    import util from '../../common/js/util'
    //import NProgress from 'nprogress'
    import {getClassInfoList, removeStuInfo, batchRemoveStu, editClassInfo} from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    StuNumber: ''
                },
                search: {
                    keyword: ''
                },
                table: {
                    loading: false,
                    total: 0,
                    size: 10,
                    page: 1,
                    pageSize: 10,
                    data: []
                },
                listLoading: false,
                sels: [],//

                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    infoName: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    stuNumber: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    stuContent: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                //
                editForm: {
                    infoName: '',
                    stuNumber: '',
                    theTime: '',
                    endTime: '',
                    stuContent: '',
                    stuOff: '',
                    stuReason: ''
                }

            }
        },
        computed: {
            getClassInfoList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size, keyword = self.search.keyword;
                var v = data.filter(function (p) {
                    return (p.classNum && p.classNum.indexOf(keyword) !== -1)
                        || (p.theTime && p.theTime.indexOf(keyword) !== -1)
                        || (p.classContent && p.classContent.indexOf(keyword) !== -1);
                });
                self.total = v.length;
                v = v.slice((page - 1) * size, page * size);
                return v;
            },
        },
        methods: {
            //
            formatSex: function (row, column) {
                return row.stuSex === 1 ? '' : row.stuSex === 0 ? '' : '';
            },
            handleSizeChange(v) {
                this.table.size = v;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            // 
            getInfos() {
                let p = this.$route.query;
                if (p.classNum === undefined) {
                    return;
                }
                let para = {
                    classNum: p.classNum
                };
                this.listLoading = true;
                //
                getClassInfoList(para).then((response) => {
                    var d = response.data;
                    if (d && d.code === 'SUCCESS') {
                        this.table.data = d.data;
                    } else {
                        util.warning(d.msg);
                    }
                    this.listLoading = false;
                });
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //
            handleAdd: function () {
                this.addFormVisible = true;
                this.addForm = {
                    stuName: '',
                    stuSex: -1,
                    stuAge: 0,
                    stuNumber: '',
                    classNum: ''
                };
            },
            //
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.editForm);
//							para.birth = (!para.birth || para.birth === '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            editStuInfo(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.addLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.addForm);
                            console.log(para);
//							para.birth = (!para.birth || para.birth === '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            addStu(para).then((res) => {
                                this.addLoading = false;
                                console.log(res);
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['addForm'].resetFields();
                                this.addFormVisible = false;
                                console.log("=========");
                                this.getUsers();
                                console.log("====");
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            }
        },
        mounted() {
            this.getInfos();
        }
    }

</script>

<style scoped>

</style>


