<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.UserName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getUsers"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="getStuUserList" highlight-current-row v-loading="listLoading" @selection-change="selsChange"
              style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="id" width="120" label="ID" sortable>
      </el-table-column>
      <el-table-column prop="userName" width="180" label="" sortable>
      </el-table-column>
      <el-table-column prop="passWord" width="180" label="" sortable>
      </el-table-column>
      <el-table-column prop="rights" min-width="110" label="">
      </el-table-column>
      <el-table-column prop="status" min-width="110" label="">
      </el-table-column>
      <el-table-column label="" width="150">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="stuAdminDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!--<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>-->
      <!---->
      <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="table.page"
              :page-sizes="[10, 25, 50, 100]"
              :page-size="table.size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="table.data.length" style="float:right">
      </el-pagination>
    </el-col>

    <!---->
    <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="userName">
          <el-input v-model="editForm.userName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="passWord">
          <el-input v-model="editForm.passWord"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rights">
          <el-input v-model="editForm.rights"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="userName">
          <el-input v-model="addForm.userName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="passWord">
          <el-input v-model="addForm.passWord"></el-input>
        </el-form-item>
        <el-form-item label="" prop="rights">
          <el-input v-model="addForm.rights"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>

  </section>
</template>

<script>
    //import NProgress from 'nprogress'
    import {addStuUser, batchRemoveUser, editStuUser, getStuUserList} from '../../api/api';

    export default {
        data() {
            return {
                filters: {
                    UserName: ''
                },
                search: {
                    keyword: ''
                },
                table: {
                    loading: false,
                    total: 0,
                    size: 10,
                    page: 1,
                    pageSize: 10,
                    data: []
                },
                listLoading: false,
                sels: [],//

                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    userName: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    passWord: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    rights: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                //
                editForm: {
                    userName: '',
                    passWord: '',
                    rights: ''
                },
                addFormVisible: false,//
                addLoading: false,
                addFormRules: {
                    userName: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    passWord: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    rights: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                addForm: {
                    userName: '',
                    passWord: '',
                    rights: ''
                }

            }
        },
        computed: {
            getStuUserList: function () {
                var self = this, data = self.table.data, page = self.table.page, size = self.table.size,
                    keyword = self.search.keyword;
                var v = data.filter(function (p) {
                    return (p.userName && p.userName.indexOf(keyword) !== -1)
                        || (p.passWord && p.passWord.indexOf(keyword) !== -1)
                        || (p.rights && p.rights.indexOf(keyword) !== -1);
                });
                self.total = v.length;
                v = v.slice((page - 1) * size, page * size);
                return v;
            }
        },
        methods: {
            handleSizeChange(v) {
                this.table.size = v;
            },
            handleCurrentChange(val) {
                this.table.page = val;
            },
            // 
            getUsers() {
                let para = {
                    userName: this.filters.UserName
                };
                this.listLoading = true;
                //
                getStuUserList(para).then((data) => {
                    this.table.data = data;
                    this.listLoading = false;
                });
            },
            //
            stuAdminDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = {stuNumber: row.stuNumber};
                    removeStuInfo(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getUsers();
                    });
                }).catch(() => {

                });
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //
            handleAdd: function () {
                this.addFormVisible = true;
                this.addForm = {
                    userName: '',
                    passWord: '',
                    rights: ''
                };
            },
            //
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.editForm);
//							para.birth = (!para.birth || para.birth === '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            editStuUser(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '',
                                    type: 'success'
                                });
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.addLoading = true;
                            //NProgress.start();
                            let para = Object.assign({}, this.addForm);
                            console.log(para);
//							para.birth = (!para.birth || para.birth === '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
                            addStuUser(para).then((res) => {
                                this.addLoading = false;
                                console.log(res.data.code);
                                //NProgress.done();
                                if (res.data.code === "FAIL") {
                                    this.$message({
                                        message: '',
                                        type: 'warning'
                                    });
                                } else {
                                    this.$message({
                                        message: '',
                                        type: 'success'
                                    });
                                }
                                this.$refs['addForm'].resetFields();
                                this.addFormVisible = false;
                                this.getUsers();
                            });
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = {ids: ids};
                    batchRemoveUser(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                        this.getUsers();
                    });
                }).catch(() => {

                });
            }
        },
        mounted() {
            this.getUsers();
        }
    }

</script>
<style scoped>

</style>


